.order {
  margin: 20px 0;
}
.order--colored {
  border-radius: 20px;
  padding: 20px;
  margin: 30px 0;
  background-color: rgba(212, 212, 212, 0.199);
}
.order__header {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  align-items: center;
  padding: 20px 0;
}
.order__header--column {
  flex-direction: column;
  align-items: center;
}
@media only screen and (max-width: 900px) {
  .order__header {
    flex-direction: column;
  }
}
.order__header h3 {
  font-size: 25px;
  text-align: center;
}
.order__header a {
  flex-shrink: 0;
  padding: 16px 32px;
  background-color: #de0000;
  border: 1px solid #de0000;
  color: #fff;
  border-radius: 10px;
  transition: background-color 0.3s, color 0.3s;
}
@media (hover: hover) {
  .order__header a:hover {
    color: #de0000;
    background-color: #fff;
  }
}
.order__content {
  display: flex;
  flex-direction: column;
  gap: 8px;
  font-size: 17px;
  padding: 20px 0;
}
.order__content a {
  color: rgb(163, 87, 37);
}