.header {
  position: fixed;
  z-index: 99;
  width: 100%;
  transition: background-color 0.3s, box-shadow 0.3s, height 0.3s, transform 0.3s;
  background-color: #fff;
}
.header__wrapper {
  position: relative;
  width: 100%;
  display: flex;
  padding: 16px 0;
  align-items: center;
  transition: padding 0.3s;
}
@media only screen and (max-width: 1200px) {
  .header__wrapper {
    padding: 10px 0;
  }
}
.header__icon {
  display: flex;
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  align-items: center;
  justify-content: center;
}
.header__social-icon {
  display: flex;
  flex-shrink: 0;
  width: 30px;
  height: 30px;
  align-items: center;
  justify-content: center;
}
.header__social-links {
  display: flex;
  gap: 10px;
  justify-content: space-between;
}
.header__callback {
  text-align: center;
  font-size: 15px;
  border-bottom: 2px dashed red;
  cursor: pointer;
}
.header__contacts {
  display: flex;
  flex-shrink: 0;
  flex-direction: column;
  gap: 10px;
  font-size: 15px;
}
.header__contacts:last-child {
  margin-left: 20px;
}
@media only screen and (max-width: 1350px) {
  .header__contacts:last-child {
    display: none;
  }
}
@media only screen and (max-width: 1000px) {
  .header__contacts {
    display: flex;
    margin: 0;
    width: max-content;
  }
  .header__contacts:last-child {
    display: flex;
    margin: 0;
  }
}
.header__contacts a {
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s;
  text-align: right;
  color: rgb(36, 36, 36);
}
.header__contacts span {
  display: flex;
  align-items: center;
  gap: 10px;
  text-transform: uppercase;
}
@media (hover: hover) {
  .header__contacts a:hover {
    color: #de0000;
  }
}
.header__logo {
  display: flex;
  flex-shrink: 0;
}
@media only screen and (max-width: 1000px) {
  .header__logo {
    margin-left: auto;
  }
}
.header__nav {
  margin-right: auto;
}
.header__nav a {
  white-space: nowrap;
  font-size: 16px;
  font-weight: 500;
  color: rgb(36, 36, 36);
  transition: color 0.3s;
}
.header__nav a.is-current {
  color: #de0000;
}
@media only screen and (max-width: 1600px) {
  .header__nav a {
    font-size: 15px;
    line-height: 20px;
  }
}
@media only screen and (max-width: 1000px) {
  .header__nav a {
    font-size: 18px;
    line-height: 22px;
  }
}
.header__nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 32px;
  margin: 0 32px;
}
@media only screen and (max-width: 1600px) {
  .header__nav ul {
    gap: 16px;
    margin: 0 20px;
  }
}
@media only screen and (max-width: 1000px) {
  .header__nav ul {
    flex-direction: column;
    margin: 0;
  }
}
@media (hover: hover) {
  .header__nav a:hover {
    color: #de0000;
  }
}
.header__burger {
  position: absolute;
  left: 0;
  top: auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  flex-shrink: 0;
  z-index: 100;
  cursor: pointer;
  display: none;
}
.header__burger i {
  display: block;
  position: relative;
  height: 2px;
  width: 60%;
  border-radius: 2px;
  background-color: #de0000;
  left: 0;
  top: 0;
  margin-bottom: 4px;
  transition: opacity 0.3s, transform 0.3s;
}
.header__burger i:last-child {
  margin-bottom: 0;
}
@media only screen and (max-width: 1000px) {
  .header__burger {
    display: flex;
  }
}
.header .menu {
  display: contents;
}
@media only screen and (max-width: 1000px) {
  .header .menu {
    display: block;
    overflow-y: scroll;
    padding-bottom: 50px;
    position: absolute;
    z-index: 99;
    top: 64px;
    left: -20px;
    background-color: rgba(255, 255, 255, 0.9254901961);
    backdrop-filter: blur(5px);
    height: auto;
    width: calc(100vw + 20px);
    pointer-events: none;
    transform: translateX(-100%);
    transition: transform 0.3s;
  }
  .header .menu::-webkit-scrollbar, .header .menu::-webkit-scrollbar-thumb {
    display: none;
  }
}
.header .menu-scroll {
  display: contents;
}
@media only screen and (max-width: 1000px) {
  .header .menu-scroll {
    display: flex;
    gap: 16px;
    flex-direction: column;
    height: auto;
    width: 100vw;
    padding: 24px 20px 4px 30px;
  }
}

body.is-nav-open {
  overflow: hidden;
}
body.is-nav-open .menu {
  transform: none;
  pointer-events: auto;
}
body.is-nav-open .header__burger i {
  transform: rotate(45deg);
}
body.is-nav-open .header__burger i:first-child {
  opacity: 0;
}
body.is-nav-open .header__burger i:last-child {
  transform: rotate(-45deg);
  top: -6px;
}

body.is-scrolled .header {
  box-shadow: 0px 11px 15px -10px rgb(36, 36, 36);
  background-color: #ffffff;
  backdrop-filter: blur(10px);
}
@media only screen and (max-width: 1000px) {
  body.is-scrolled .header {
    background-color: rgba(255, 255, 255, 0.9254901961);
    backdrop-filter: none;
  }
}
body.is-scrolled .header__wrapper {
  position: relative;
  width: 100%;
  display: flex;
  padding: 6px 0;
  align-items: center;
}