.rewards__wrapper {
  display: flex;
  gap: 64px;
}
@media only screen and (max-width: 1300px) {
  .rewards__wrapper {
    gap: 24px;
  }
}
@media only screen and (max-width: 900px) {
  .rewards__wrapper {
    flex-direction: column-reverse;
  }
}
.rewards__list {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  width: calc(50% - 32px);
}
@media only screen and (max-width: 1300px) {
  .rewards__list {
    width: calc(50% - 12px);
  }
}
@media only screen and (max-width: 900px) {
  .rewards__list {
    width: 100%;
  }
}
.rewards__text {
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: calc(50% - 32px);
}
@media only screen and (max-width: 1300px) {
  .rewards__text {
    width: calc(50% - 12px);
  }
}
@media only screen and (max-width: 900px) {
  .rewards__text {
    width: 100%;
  }
}
.rewards__text h2 {
  font-size: 42px;
  font-weight: 600;
  line-height: 53.55px;
  text-align: left;
  text-transform: uppercase;
}
@media only screen and (max-width: 1300px) {
  .rewards__text h2 {
    font-size: 32px;
    line-height: 43.55px;
  }
}
@media only screen and (max-width: 900px) {
  .rewards__text h2 {
    text-align: center;
  }
}
.rewards__text p {
  font-size: 18px;
  font-weight: 300;
  line-height: 27px;
  text-align: left;
}
@media only screen and (max-width: 900px) {
  .rewards__text p {
    font-size: 16px;
    line-height: 22px;
    text-align: center;
  }
}
.rewards__item {
  width: calc(50% - 6px);
  position: relative;
  min-height: 428px;
  border: 1px solid rgba(24, 25, 31, 0.1607843137);
  border-radius: 16px;
}
@media only screen and (max-width: 900px) {
  .rewards__item {
    min-height: 328px;
  }
}
@media only screen and (max-width: 700px) {
  .rewards__item {
    width: 100%;
  }
}
.rewards__item img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  padding: 40px;
  object-fit: contain;
}
@media only screen and (max-width: 1300px) {
  .rewards__item img {
    padding: 20px;
  }
}