.button {
  display: inline-flex;
  align-items: center;
  position: relative;
  text-align: center;
  justify-content: center;
  cursor: pointer;
  text-decoration: none;
  transition: background-color 0.3s, opacity 0.3s, box-shadow 0.3s;
  font-size: 18px;
  font-weight: 700;
  line-height: 22.95px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #fff;
  background-color: #de0000;
  padding: 24px 16px;
  border-radius: 8px;
  border: none;
}
@media (hover: hover) {
  .button:hover {
    background-color: #932d2c;
  }
}
@media only screen and (max-width: 1800px) {
  .button {
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
    padding: 16px;
  }
}
.button--lg {
  min-width: 420px;
  font-size: 24px;
  font-weight: 800;
  line-height: 30.6px;
  text-align: left;
}
@media (max-width: 1000px) {
  .button--lg {
    min-width: auto;
    font-size: 18px;
    font-weight: 600;
    line-height: 24px;
  }
}
.button--animated {
  position: relative;
  z-index: 2;
}
.button--animated div {
  position: absolute;
  inset: 0;
  z-index: -1;
  background-color: rgba(204, 65, 63, 0.8392156863);
  width: 100%;
  height: 100%;
  border-radius: 8px;
  pointer-events: none;
  animation: pulse 2s infinite ease-in-out;
}

@keyframes pulse {
  100% {
    transform: scaleY(1.7) scaleX(1.2);
    opacity: 0;
  }
}