.map {
  background-color: #18191F;
  border-radius: 16px;
  overflow: hidden;
  background-image: url("../img/bgr.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  animation: moveBackground 33s ease-in-out infinite alternate;
}
.map__caption {
  margin-left: 42px;
  margin-top: 96px;
  color: #fff;
  padding: 0 12px;
}
.map__caption h2 {
  font-size: 64px;
  font-weight: 500;
  line-height: 81.6px;
  text-align: left;
  text-transform: uppercase;
}
@media only screen and (max-width: 1300px) {
  .map__caption h2 {
    text-align: center;
    font-size: 34px;
    font-weight: 500;
    line-height: 41.6px;
  }
}
.map__caption span {
  display: inline-block;
  font-size: 24px;
  font-weight: 300;
  line-height: 36px;
  text-align: left;
  color: rgba(255, 255, 255, 0.6392156863);
  width: 50%;
}
@media only screen and (max-width: 1300px) {
  .map__caption span {
    width: 100%;
    text-align: center;
    margin-top: 1rem;
  }
}
@media only screen and (max-width: 900px) {
  .map__caption span {
    font-size: 20px;
    line-height: 24px;
  }
}
@media only screen and (max-width: 1300px) {
  .map__caption {
    margin: 46px 0;
  }
}
.map__content {
  display: flex;
  width: 100%;
  position: relative;
  gap: 96px;
  padding: 96px 42px;
}
@media only screen and (max-width: 1700px) {
  .map__content {
    gap: 46px;
    padding: 36px;
  }
}
@media only screen and (max-width: 1300px) {
  .map__content {
    flex-direction: column;
    align-items: center;
    padding: 30px 20px;
  }
  .map__content--steps {
    flex-direction: row;
  }
}
@media only screen and (max-width: 1300px) and (max-width: 1300px) {
  .map__content--steps {
    flex-direction: column;
    gap: 20px;
  }
}
.map__preview {
  position: relative;
  width: 708px;
  flex-shrink: 0;
}
@media only screen and (max-width: 770px) {
  .map__preview {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
}
.map__preview p {
  font-size: 18px;
  font-weight: 300;
  line-height: 27px;
  text-align: center;
  color: rgba(255, 255, 255, 0.6392156863);
}
.map__preview p:last-child {
  margin-bottom: 100px;
}
@media only screen and (max-width: 1300px) {
  .map__preview p:last-child {
    margin-bottom: 10px;
  }
}
@media only screen and (max-width: 770px) {
  .map__preview p {
    font-size: 16px;
    line-height: 20px;
  }
}
.map__info {
  position: absolute;
  top: 200px;
  left: 0;
  display: flex;
  flex-direction: column;
  gap: 32px;
  align-items: center;
}
.map__info span {
  font-size: 24px;
  font-weight: 300;
  line-height: 30.6px;
  text-align: center;
  color: rgba(255, 255, 255, 0.6392156863);
}
.map__info h3 {
  font-size: 30px;
  font-weight: 600;
  line-height: 38.25px;
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  margin-bottom: 24px;
}
@media only screen and (max-width: 770px) {
  .map__info h3 {
    font-size: 24px;
    line-height: 30px;
  }
}
@media only screen and (max-width: 770px) {
  .map__info {
    position: relative;
    top: auto;
    left: auto;
  }
}
.map__info--text {
  position: relative;
  top: auto;
  left: auto;
}
.map__data {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  margin-top: 0px;
}
.map__data-item {
  opacity: 0;
  transition: opacity 0.3s;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding: 24px;
  border-radius: 8px;
  flex-grow: 1;
  background-color: #24252A;
  color: #fff;
  max-height: 300px;
}
.map__data-item span {
  font-size: 16px;
  font-weight: 300;
  line-height: 20.4px;
  text-align: left;
  color: rgba(255, 255, 255, 0.6392156863);
  margin-top: 1rem;
}
.map__data-item h2 {
  font-size: 30px;
  font-weight: 600;
  line-height: 38.25px;
  text-align: left;
  text-transform: uppercase;
}
.map__data-item.is-visible {
  opacity: 1;
}
.map__data-heading h2 {
  font-size: 72px;
  font-weight: 500;
  line-height: 91.8px;
  text-align: left;
}
@media only screen and (max-width: 1700px) {
  .map__data-heading h2 {
    font-size: 62px;
    font-weight: 500;
    line-height: 71.8px;
  }
}
.map__data-heading span {
  color: #fff;
}
.map__data-heading--sm h2 {
  font-size: 30px;
  font-weight: 600;
  line-height: 38.25px;
  text-align: left;
}
@media only screen and (max-width: 1700px) {
  .map__data-heading--sm h2 {
    font-size: 26px;
    line-height: 30px;
  }
}
@media only screen and (max-width: 1300px) {
  .map__data {
    flex-direction: column;
  }
}
.map__wrapper {
  display: flex;
  gap: 12px;
  flex-direction: column;
  width: calc(50% - 6px);
}
.map__wrapper:nth-child(2) {
  margin-top: 100px;
  margin-bottom: -100px;
}
@media only screen and (max-width: 1300px) {
  .map__wrapper:nth-child(2) {
    margin: 0;
  }
}
@media only screen and (max-width: 1300px) {
  .map__wrapper {
    width: 100%;
  }
}
.map__step {
  display: flex;
  position: relative;
  z-index: 1;
  flex-shrink: 0;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.0509803922);
  padding: 30px 20px;
  border-radius: 8px;
  max-width: 400px;
  border: 1px solid transparent;
  transition: background-color 0.3s;
  animation: bgColorChange 3s ease-in-out infinite alternate;
}
@media only screen and (max-width: 1300px) {
  .map__step {
    padding: 16px;
    animation: none;
  }
}
.map__step-heading {
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 42px;
}
.map__step-heading span {
  font-size: 72px;
  font-weight: 500;
  line-height: 91.8px;
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 10px;
}
.map__step-heading h2 {
  font-size: 20px;
  font-weight: 600;
  line-height: 25.5px;
  text-transform: uppercase;
  text-align: center;
}
.map__step-heading p {
  font-size: 16px;
  font-weight: 300;
  line-height: 20.4px;
  color: rgba(255, 255, 255, 0.6392156863);
  text-align: center;
}
.map__step.is-current {
  background-color: rgba(255, 0, 0, 0.1098039216);
  border: 1px solid #fff;
}
.map__steps {
  flex-shrink: 0;
  position: absolute;
  right: 0;
  top: calc(50% - 252px);
  width: 1760px;
  height: 505px;
  object-fit: contain;
}
@media only screen and (max-width: 1600px) {
  .map__steps {
    right: -60px;
    top: calc(50% - 302px);
  }
}
@media only screen and (max-width: 1300px) {
  .map__steps {
    display: none;
  }
}
.map__steps-mobile {
  display: none;
  flex-wrap: wrap;
  gap: 20px;
}
@media only screen and (max-width: 1300px) {
  .map__steps-mobile {
    display: flex;
    flex-direction: column;
  }
}
.map__step-icon--first img {
  display: none;
}
@media only screen and (max-width: 1300px) {
  .map__step-icon--first img {
    display: block;
  }
}
.map__order {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  width: 100%;
  padding: 64px 46px;
}
@media only screen and (max-width: 1500px) {
  .map__order {
    padding: 26px;
  }
}
@media only screen and (max-width: 1300px) {
  .map__order {
    flex-wrap: wrap;
    padding: 16px;
  }
}
.map__order div {
  opacity: 0;
  transition: opacity 0.3s;
  padding: 24px;
  background-color: rgba(36, 37, 42, 0.9607843137);
  width: calc(33% - 6px);
  border-radius: 8px;
}
@media only screen and (max-width: 1300px) {
  .map__order div {
    width: 100%;
  }
}
.map__order div header {
  color: #fff;
  text-transform: uppercase;
  display: flex;
  width: 100%;
  gap: 6px;
  align-items: center;
  margin-bottom: 40px;
}
@media only screen and (max-width: 1800px) {
  .map__order div header {
    flex-wrap: wrap;
  }
}
.map__order div header span {
  font-size: 72px;
  font-weight: 500;
  line-height: 91.8px;
  text-align: center;
}
.map__order div header h3 {
  font-size: 30px;
  font-weight: 600;
  line-height: 38.25px;
  text-align: left;
}
@media only screen and (max-width: 1300px) {
  .map__order div header h3 {
    font-size: 26px;
    line-height: 30px;
  }
}
@media only screen and (max-width: 900px) {
  .map__order div header h3 {
    font-size: 18px;
    line-height: 22px;
  }
}
.map__order div h4 {
  font-size: 16px;
  font-weight: 500;
  line-height: 20.4px;
  text-align: left;
  color: #fff;
}
.map__order div ul {
  color: rgba(255, 255, 255, 0.6392156863);
  padding-left: 1rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
}
.map__order div ul > li {
  margin-bottom: 8px;
}
.map__order div.is-visible {
  opacity: 1;
}

@keyframes moveBackground {
  0% {
    background-position: center left;
    background-size: 100%;
  }
  50% {
    background-position: center right;
    background-size: 150%;
  }
  100% {
    background-position: center center;
    background-size: 100%;
  }
}
@keyframes bgColorChange {
  0% {
    background-color: rgba(255, 255, 255, 0.0509803922);
  }
  50% {
    background-color: rgba(255, 0, 0, 0.1098039216);
    border-color: #fff;
  }
  100% {
    background-color: rgba(255, 255, 255, 0.0509803922);
  }
}