html,
body {
  min-height: 100%;
  position: relative;
  scroll-behavior: smooth;
  scrollbar-gutter: stable;
}

body {
  text-wrap: pretty;
  color: rgb(36, 36, 36);
  font-family: "Montserrat", serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
  font-size: 14px;
  line-height: 1.4;
}
body.is-popup-open {
  overflow: hidden;
}

h2 {
  font-size: 48px;
  line-height: 48px;
}
@media only screen and (max-width: 1200px) {
  h2 {
    font-size: 38px;
    line-height: 38px;
  }
}
@media only screen and (max-width: 800px) {
  h2 {
    font-size: 28px;
    line-height: 28px;
  }
}

.all {
  display: inline-block;
  color: #dd9933;
  padding-top: 6px;
  border-top: 2px solid #000;
  margin: 20px 0;
  font-size: 20px;
}
@media (hover: hover) {
  .all:hover {
    color: #de0000;
  }
}

.callback {
  text-align: center;
  background-color: #fff;
  padding: 10px;
}
.callback form {
  box-shadow: none;
}
.callback h2 {
  font-size: 30px;
  margin: 0;
}
@media only screen and (max-width: 900px) {
  .callback h2 {
    font-size: 25px;
  }
}
.callback__caption {
  font-weight: 600;
  text-align: left;
}
.callback__notice {
  display: inline-block;
  font-weight: 300;
  margin: 10px 0;
}

input::placeholder {
  color: rgb(221, 221, 221);
  font-size: 15px;
}

textarea::placeholder {
  color: rgb(221, 221, 221);
  font-size: 15px;
}

.page-controls {
  color: rgb(221, 221, 221);
  border-bottom: 1px solid rgb(221, 221, 221);
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.page-controls__item {
  display: flex;
  flex-direction: column;
}
.page-controls__item--next span {
  display: inline-block;
  width: 100%;
  text-align: right;
}
.page-controls span {
  text-transform: uppercase;
}
.page-controls a {
  margin-top: 6px;
  padding-bottom: 6px;
  display: block;
  color: rgb(221, 221, 221);
}

.wrapper {
  display: flex;
  flex-flow: column nowrap;
  min-height: 100vh;
  overflow: hidden;
}

.main {
  position: relative;
  flex-grow: 1;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 32px;
}
@media only screen and (max-width: 1300px) {
  .container {
    padding: 0 10px;
  }
}

img {
  max-width: 100%;
  vertical-align: bottom;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

*:focus {
  outline: none !important;
}

canvas {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  transition: color 0.3s;
}

p {
  margin: 1em 0;
}
p:first-child {
  margin-top: 0;
}
p:last-child {
  margin-bottom: 0;
}

button {
  background-color: transparent;
  padding: 0;
  border: none;
  user-select: none;
  cursor: pointer;
}

img {
  max-width: 100%;
}

.svg-icon {
  fill: currentColor;
}

.icon {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

a,
input,
label,
button {
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-tap-highlight-color: transparent;
}

a,
input,
label,
span,
button::selection {
  background: transparent;
}

a,
input,
label,
button::-moz-selection {
  background: transparent;
}

.section-caption {
  font-size: 48px;
  font-weight: 500;
  line-height: 62.4px;
  color: #555555;
}
.section-caption--white {
  color: #fff;
}
@media only screen and (max-width: 1000px) {
  .section-caption {
    font-size: 28px;
    line-height: 32px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

