.scrollable-container {
  display: contents;
}
@media only screen and (max-width: 900px) {
  .scrollable-container {
    display: block;
    height: calc(100vh - 100px);
    overflow-y: scroll;
    padding-bottom: 50px;
  }
  .scrollable-container::-webkit-scrollbar, .scrollable-container::-webkit-scrollbar-thumb {
    display: none;
  }
}

