.modal {
  display: none;
  border-radius: 16px;
  width: 610px;
}
.modal .contacts__item:first-child {
  width: 100%;
  background-color: #fff;
  color: #000;
  text-align: center;
}
.modal .form {
  margin: 0 86px;
}
.modal .form__button {
  margin-top: 32px;
  height: 68px;
}
@media only screen and (max-width: 700px) {
  .modal .form__button {
    margin-top: 18px;
  }
}
@media only screen and (max-width: 1000px) {
  .modal .form {
    margin: 0;
  }
}
.modal .fancybox-button svg {
  display: none;
}
.modal .fancybox-button::before {
  content: "x";
  display: inline-block;
  width: 12px;
  height: 12px;
  color: #de0000;
  font-weight: 700;
  font-size: 18px;
}

body .fancybox-content {
  padding: 0 !important;
  background-color: #fff;
  scrollbar-width: none;
}