@charset "UTF-8";
.service-page {
  position: relative;
  padding: 40px 0;
  margin-top: 132px;
  margin-bottom: 32px;
}
.service-page h2 {
  font-size: 30px;
  text-align: center;
  margin-bottom: 20px;
}
@media only screen and (max-width: 900px) {
  .service-page h2 {
    font-size: 25px;
  }
}
@media only screen and (max-width: 1200px) {
  .service-page {
    margin-top: 98px;
  }
}
@media only screen and (max-width: 900px) {
  .service-page {
    margin-top: 70px;
  }
}
.service-page__banner {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 500px;
  border-radius: 30px;
  padding: 40px;
  margin: 30px 0;
}
.service-page__banner ul {
  font-size: 16px;
  width: max-content;
  column-count: 2; /* Указываем количество колонок */
  list-style-type: disc; /* Тип маркера списка */
  padding-left: 20px; /* Отступ слева от списка */
}
.service-page__banner ul li {
  margin-bottom: 6px;
}
@media only screen and (max-width: 1200px) {
  .service-page__banner {
    padding: 20px;
  }
}
.service-page__banner .order {
  position: relative;
  z-index: 1;
  margin-top: auto;
}
.service-page__banner img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.service-page__banner-text {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.service-page__banner-text span {
  font-size: 16px;
  font-weight: 600;
}
.service-page .avto {
  margin: 30px 0;
}
.service-page .avto__wrapper {
  display: flex;
  gap: 20px;
}
.service-page .avto__wrapper div {
  border-radius: 20px;
  overflow: hidden;
}
@media only screen and (max-width: 900px) {
  .service-page .avto__wrapper {
    flex-direction: column;
  }
}
.service-page__icon {
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  border-radius: 10px;
  background-color: #de0000;
  font-size: 32px;
  color: #fff;
}
.service-page__algoritm-item {
  display: flex;
  flex-direction: column;
  width: calc(50% - 15px);
}
@media only screen and (max-width: 1200px) {
  .service-page__algoritm-item {
    width: 100%;
  }
}
.service-page__algoritm-item ul {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 16px;
  margin-top: 16px;
}
.service-page__algoritm-wrapper {
  margin: 30px 0;
  display: flex;
  gap: 30px;
}
@media only screen and (max-width: 1200px) {
  .service-page__algoritm-wrapper {
    flex-direction: column;
  }
}
.service-page__algoritm-sublist ul {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}
.service-page__algoritm-sublist ul i {
  color: #de0000;
  font-size: 30px;
}
.service-page__algoritm-sublist ul li {
  width: 50%;
  margin-bottom: 20px;
}
@media only screen and (max-width: 1200px) {
  .service-page__algoritm-sublist ul li {
    width: 100%;
  }
}
.service-page__algoritm-header {
  display: flex;
  align-items: center;
  gap: 20px;
}
.service-page__algoritm-header h3 {
  font-size: 23px;
}
.service-page__algoritm-notice {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #de0000;
  display: flex;
  border-radius: 10px;
  color: #fff;
  padding: 20px;
  margin: 20px 0;
}
.service-page__algoritm-notice p {
  display: flex;
  justify-content: center;
  border: 2px solid #fff;
  width: 100%;
  height: 100%;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}
.service-page .dms__callback {
  margin: 30px 0;
  box-shadow: 0 11.9104px 37.4328px rgba(148, 143, 143, 0.25);
  justify-content: center;
}
.service-page__prices, .service-page .how {
  font-size: 16px;
  padding: 20px;
  margin: 30px 0;
  background-color: rgba(212, 212, 212, 0.199);
}
.service-page__prices .order__header, .service-page .how .order__header {
  justify-content: center;
}
.service-page .how .service-page__algoritm-wrapper {
  flex-wrap: wrap;
}
.service-page .how .service-page__algoritm-header {
  flex-direction: column;
}
.service-page .how .service-page__algoritm-item {
  text-align: center;
  width: calc(31% - 15px);
}
@media only screen and (max-width: 1200px) {
  .service-page .how .service-page__algoritm-item {
    width: 100%;
  }
}
.service-page .spec-list {
  margin: 30px 0;
}
.service-page .spec-list__wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
@media only screen and (max-width: 900px) {
  .service-page .spec-list__wrapper {
    flex-direction: column;
  }
}
.service-page .spec-list__item {
  width: calc(32% - 10px);
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
  text-align: center;
}
@media only screen and (max-width: 900px) {
  .service-page .spec-list__item {
    width: 100%;
  }
}
.service-page .spec-list__photo {
  height: 180px;
  width: 130px;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
}
.service-page .spec-list__photo img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.service-page .guarantees {
  margin: 30px 0;
}
.service-page .guarantees__list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
@media only screen and (max-width: 900px) {
  .service-page .guarantees__list {
    flex-direction: column;
  }
}
.service-page .guarantees__item {
  width: calc(32% - 10px);
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
  padding: 34px;
  text-align: center;
  box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.09);
}
@media only screen and (max-width: 900px) {
  .service-page .guarantees__item {
    width: 100%;
  }
}
.service-page .text-block {
  display: flex;
  margin: 30px 0;
  gap: 20px;
}
.service-page .text-block__content {
  width: 50%;
  font-size: 16px;
  line-height: 25px;
}
.service-page .text-block__content a {
  color: #dd9933;
}
.service-page .text-block__content .order__header a {
  color: #fff;
}
.service-page .text-block__content .order__header a:hover {
  color: #de0000;
}
@media only screen and (max-width: 1200px) {
  .service-page .text-block__content {
    width: 100%;
  }
}
.service-page .text-block__gallery {
  display: flex;
  flex-wrap: wrap;
  width: 50%;
  gap: 20px;
  min-height: 100%;
}
@media only screen and (max-width: 1200px) {
  .service-page .text-block__gallery {
    display: none;
  }
}
.service-page .text-block__gallery div {
  width: calc(50% - 10px);
  height: calc(50% - 10px);
  position: relative;
  border-radius: 20px;
  overflow: hidden;
}
.service-page .text-block__gallery div img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.service-page .text-block__img {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
  min-height: 100%;
  position: relative;
  padding: 20px;
}
@media only screen and (max-width: 1200px) {
  .service-page .text-block__img {
    display: none;
  }
}
.service-page .text-block__img p {
  position: relative;
  z-index: 1;
  font-weight: 800;
  font-size: 45px;
  text-align: center;
  color: #fff;
  margin: 0;
}
.service-page .text-block__img img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.service-page .text-block__img--contain img {
  object-fit: contain;
}
.service-page .more {
  margin: 30px 0;
}
.service-page .more__list {
  display: flex;
  gap: 30px;
}
@media only screen and (max-width: 900px) {
  .service-page .more__list {
    flex-direction: column;
  }
}
.service-page .more__list a {
  width: calc(32% - 15px);
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  border: 2px solid rgb(223, 223, 223);
  padding: 30px 20px;
  border-radius: 20px;
  cursor: pointer;
}
@media only screen and (max-width: 900px) {
  .service-page .more__list a {
    width: 100%;
  }
}
.service-page .more__list a span {
  margin-top: auto;
  font-weight: 600;
  padding: 16px 32px;
  background-color: #de0000;
  border: 2px solid #de0000;
  color: #fff;
  border-radius: 10px;
  transition: background-color 0.3s, color 0.3s;
  box-shadow: 0px 1px 11px 0px rgba(0, 0, 0, 0.5);
}
.service-page .more__list a h3 {
  color: rgb(36, 36, 36);
  font-size: 30px;
  font-weight: 700;
  text-align: center;
}
@media only screen and (max-width: 1200px) {
  .service-page .more__list a h3 {
    font-size: 24px;
  }
}
.service-page .more__list a div {
  width: 150px;
  height: 150px;
  position: relative;
  overflow: hidden;
}
@media only screen and (max-width: 1200px) {
  .service-page .more__list a div {
    width: 100px;
    height: 100px;
  }
}
.service-page .more__list a div img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (hover: hover) {
  .service-page .more__list a:hover span {
    color: #de0000;
    background-color: #fff;
  }
}
.service-page__block {
  display: flex;
  gap: 20px;
  margin: 40px 0;
}
.service-page__block h3 {
  font-size: 33px;
  font-weight: 700;
  text-align: left;
}
.service-page__block--column-gallery .service-page__block-content {
  width: 65%;
}
@media only screen and (max-width: 1100px) {
  .service-page__block--column-gallery .service-page__block-content {
    width: 100%;
  }
}
.service-page__block--column-gallery .service-page__block-gallery {
  flex-direction: column;
  width: 35%;
}
.service-page__block--column-gallery .service-page__block-gallery .service-page__block-gallery-item {
  width: 100%;
  height: 222px;
}
.service-page__block-content {
  width: calc(50% - 10px);
}
@media only screen and (max-width: 1100px) {
  .service-page__block-content {
    width: 100%;
  }
}
.service-page__block-bordered {
  padding: 20px;
  border: 2px solid rgb(236, 236, 236);
  margin: 20px 0;
}
.service-page__block-gallery {
  width: calc(50% - 10px);
  display: flex;
  gap: 20px;
}
@media only screen and (max-width: 1100px) {
  .service-page__block-gallery {
    display: none;
  }
}
.service-page__block-gallery-item {
  position: relative;
  height: 450px;
  width: 50%;
  overflow: hidden;
  border-radius: 30px;
}
.service-page__block-gallery-item img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.service-page .hero__labels {
  width: 50%;
}
@media only screen and (max-width: 900px) {
  .service-page .hero__labels {
    width: 100%;
  }
}