.services--page {
  padding: 40px 0;
  margin-top: 132px;
  margin-bottom: 32px;
}
@media only screen and (max-width: 1200px) {
  .services--page {
    margin-top: 98px;
  }
}
@media only screen and (max-width: 900px) {
  .services--page {
    margin-top: 70px;
  }
}
.services__list {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  margin: 32px 0;
  width: 100%;
}
.services__item {
  color: #fff;
  flex-grow: 1;
  max-width: calc(33% - 10px);
  position: relative;
  display: flex;
  padding: 10px;
  flex-direction: column;
  justify-content: space-between;
  height: 230px;
  border-radius: 16px;
  overflow: hidden;
  background: linear-gradient(111.82deg, #ff0000 2.11%, #a10000 95.27%), #F5F5F5;
}
.services__item h3 {
  font-size: 19px;
  line-height: 25px;
  font-weight: 700;
  width: 50%;
  text-transform: uppercase;
  transition: opacity 0.3s;
}
.services__item span {
  font-size: 14px;
  text-decoration: underline;
  transition: opacity 0.3s, text-decoration 0.3s;
}
@media (hover: hover) {
  .services__item:hover h3 {
    opacity: 0.5;
  }
  .services__item:hover span {
    opacity: 0.5;
    text-decoration: none;
  }
}
@media only screen and (max-width: 900px) {
  .services__item {
    height: 160px;
    width: 100%;
    max-width: 100%;
  }
}
.services__item-icon {
  width: 150px;
  height: 150px;
  position: absolute;
  bottom: 5px;
  right: 5px;
}
@media only screen and (max-width: 900px) {
  .services__item-icon {
    width: 100px;
    height: 100px;
  }
}