.pager {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
  gap: 8px;
}
.pager__number {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  width: 56px;
  height: 56px;
  border-radius: 8px;
  font-size: 20px;
  font-weight: 500;
  line-height: 30px;
  color: rgba(24, 25, 31, 0.6392156863);
  transition: opacity 0.5s;
}
@media (hover: hover) {
  .pager__number.current:hover, .pager__number.disabled:hover {
    opacity: 1;
  }
  .pager__number:hover {
    opacity: 0.5;
  }
}
@media (max-width: 399px) {
  .pager__number {
    width: 30px;
    height: 30px;
  }
}
.pager__dots {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  font-size: 14px;
  font-weight: 600;
  line-height: 17.85px;
  color: #AA804F;
}
.pager .current {
  color: #FFFFFF;
  background-color: #de0000;
  pointer-events: none;
}
.pager .disabled {
  opacity: 0.5;
  pointer-events: none;
}
.pager__icon {
  border: solid rgba(24, 25, 31, 0.6392156863);
  border-width: 0 2px 2px 0;
  padding: 2px;
  border-radius: 0;
  display: inline-block;
  min-width: 10px;
  height: 10px;
  transform: rotate(-45deg);
  margin-right: 2px;
}
.pager__next, .pager__prev {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background-color: #ffffff;
  cursor: pointer;
}
.pager__prev .pager__icon {
  transform: rotate(135deg);
  margin-left: 2px;
  margin-right: 0;
}