.breadcrumbs {
  display: flex;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
  padding: 2px;
  background-color: #383838;
}
@media (max-width: 1000px) {
  .breadcrumbs {
    overflow-x: scroll;
  }
  .breadcrumbs::-webkit-scrollbar, .breadcrumbs::-webkit-scrollbar-thumb {
    display: none;
  }
}
.breadcrumbs a {
  font-size: 14px;
  font-weight: 400;
  line-height: 18.2px;
  color: #dd9933;
}
@media (max-width: 700px) {
  .breadcrumbs a {
    font-size: 10px;
    line-height: 14px;
  }
}
.breadcrumbs .breadcrumb_font_style {
  margin-right: 10px;
}
.breadcrumbs .breadcrumb_font_dash {
  color: rgba(255, 255, 255, 0.6392156863);
}
.breadcrumbs ol {
  flex-shrink: 0;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  width: max-content;
  font-size: 14px;
  font-weight: 400;
  line-height: 18.2px;
  color: #dd9933;
}
@media (max-width: 700px) {
  .breadcrumbs ol {
    font-size: 10px;
    line-height: 14px;
  }
}
.breadcrumbs ol li:not(:last-child) {
  margin-right: 10px;
}
.breadcrumbs ol li:last-child .breadcrumb_font_style {
  margin-right: 10px;
  color: rgba(221, 153, 51, 0.5137254902);
}
.breadcrumbs .span_m_l_20 {
  margin-left: 15px;
}