.why {
  margin-bottom: 32px;
}
.why__wrapper {
  display: flex;
}
@media only screen and (max-width: 1000px) {
  .why__wrapper {
    flex-direction: column;
    gap: 20px;
  }
}
.why__icons {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.why__icon {
  width: 100px;
  height: 100px;
  margin: 0 auto;
  margin-top: 10px;
}
.why__pics {
  display: flex;
  gap: 20px;
  margin-left: 20px;
  height: max-content;
}
@media only screen and (max-width: 1000px) {
  .why__pics {
    margin: 0 auto;
  }
}
@media only screen and (max-width: 800px) {
  .why__pics {
    display: none;
  }
}
.why__img {
  position: relative;
  width: 301px;
  height: 476px;
}
.why__img img {
  position: absolute;
  inset: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.why__pics-group {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.why__pics-group .why__img {
  width: 301px;
  height: 226px;
  border-radius: 30px;
  overflow: hidden;
}
.why__list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 0;
  list-style-position: inside;
  margin-top: 32px;
  font-size: 17px;
}
.why__item {
  position: relative;
}
@media only screen and (max-width: 1000px) {
  .why__item {
    text-align: center;
  }
}