.reviews-section {
  margin: 60px 0;
}
@media (max-width: 900px) {
  .reviews-section {
    margin: 32px 0;
  }
}
.reviews-section--page {
  padding: 40px 0;
  margin-top: 132px;
  margin-bottom: 32px;
}
@media only screen and (max-width: 1200px) {
  .reviews-section--page {
    margin-top: 98px;
  }
}
@media only screen and (max-width: 900px) {
  .reviews-section--page {
    margin-top: 70px;
  }
}
.reviews-section--page .reviews {
  padding: 0;
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}
@media only screen and (max-width: 900px) {
  .reviews-section--page .reviews {
    gap: 10px;
  }
}
.reviews-section--page .review {
  height: auto;
  box-shadow: none;
  background-color: #F4F4F4;
  border-radius: 30px;
  width: calc(32% - 10px);
  flex-grow: 1;
}
@media only screen and (max-width: 900px) {
  .reviews-section--page .review {
    width: 100%;
  }
}
.reviews-section--page .review p {
  overflow: visible;
  -webkit-line-clamp: unset;
}
.reviews-section--page .review header {
  display: flex;
  gap: 10px;
  padding-bottom: 0;
  border-bottom: none;
}

.reviews {
  margin-top: 50px;
  padding: 0 100px 100px 100px;
  position: relative;
}
@media (max-width: 900px) {
  .reviews {
    padding: 0 0 30px 0;
    margin-top: 30px;
  }
}
.reviews__controls {
  position: absolute;
  display: flex;
  justify-content: space-between;
  top: calc(40% - 33px);
  left: 0;
  width: 100%;
}
.reviews__controls .prev,
.reviews__controls .next {
  width: 64px;
  height: 64px;
  cursor: pointer;
}
.reviews__controls .next {
  transform: rotate(180deg);
}
@media (max-width: 950px) {
  .reviews__controls {
    display: none;
  }
}
.reviews .slick-slide {
  width: 330px !important;
}
.reviews .slick-track {
  display: flex !important;
  align-items: stretch !important;
}
.reviews .slick-list {
  margin: 0 -5px;
}
.reviews .slick-slide {
  position: relative;
  width: 100%;
  height: 100%;
}
.reviews .slick-slide img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.reviews .slick-dots {
  position: absolute;
  bottom: -30px;
  display: flex;
  list-style: none;
  justify-content: center;
  width: 100%;
}
.reviews .slick-dots li {
  margin: 0 10px;
}
.reviews .slick-dots button {
  font-size: 0;
  line-height: 0;
  padding: 8px;
  cursor: pointer;
  color: transparent;
  border: none;
  background-color: #ccc;
  border-radius: 50%;
  transform: scale(0.6);
  transition: transform 0.3s;
}
.reviews .slick-dots .slick-active button {
  background-color: #333;
  transform: none;
}

.review {
  height: 340px;
  padding: 20px;
  margin: 6px;
  box-shadow: 0 0 7.27941px rgba(0, 0, 0, 0.05);
  display: flex;
  flex-direction: column;
}
.review div .fa-star {
  margin-top: 10px;
  color: #fabf35;
}
.review--review {
  height: auto;
}
.review .fancybox {
  margin-top: auto;
  color: #a3a3a3;
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  transition: text-decoration 0.3s;
}
@media (hover: hover) {
  .review .fancybox:hover {
    text-decoration: underline;
  }
}
.review header {
  display: flex;
  gap: 10px;
  padding-bottom: 16px;
  border-bottom: 1px solid #c4c4c4;
}
.review p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;
}
.review__photo {
  width: 100px;
  height: 100px;
  border-radius: 8px;
  overflow: hidden;
  position: relative;
}
.review__photo img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}