.price {
  margin-bottom: 32px;
}
.price--page {
  padding: 40px 0;
  margin-top: 132px;
  margin-bottom: 32px;
}
@media only screen and (max-width: 1200px) {
  .price--page {
    margin-top: 98px;
  }
}
@media only screen and (max-width: 900px) {
  .price--page {
    margin-top: 70px;
  }
}
.price table {
  width: 100%;
  margin: 32px 0;
  border-collapse: collapse;
  text-align: left;
}
.price td td {
  text-align: left;
}
.price th, .price td {
  border: 1px solid #ddd;
  padding: 8px;
}
@media only screen and (max-width: 900px) {
  .price th, .price td {
    padding: 4px;
  }
}
.price th {
  background-color: #f4f4f4;
  font-weight: bold;
}
.price tr:nth-child(even) {
  background-color: #f9f9f9;
}
.price td[colspan="2"] {
  font-weight: bold;
}