.dms {
  padding: 40px 0;
  margin-top: 132px;
  margin-bottom: 32px;
}
.dms h2 {
  font-size: 30px;
  text-align: center;
  margin-bottom: 50px;
}
@media only screen and (max-width: 900px) {
  .dms h2 {
    font-size: 25px;
    margin-bottom: 30px;
  }
}
@media only screen and (max-width: 1200px) {
  .dms {
    margin-top: 98px;
  }
}
@media only screen and (max-width: 900px) {
  .dms {
    margin-top: 70px;
  }
}
.dms__banner {
  position: relative;
  overflow: hidden;
  height: 500px;
  border-radius: 30px;
  padding: 40px;
  margin: 30px 0;
}
.dms__banner img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.dms__banner-text {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 70%;
}
.dms__banner-text span:first-child {
  font-size: 33px;
  font-weight: 700;
}
.dms__banner-text span:last-child {
  font-size: 20px;
}
.dms__services {
  padding: 20px;
  background-color: rgb(252, 252, 252);
}
.dms__services-list {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
}
@media only screen and (max-width: 900px) {
  .dms__services-list {
    flex-direction: column;
    gap: 20px;
  }
}
.dms__icon {
  flex-shrink: 0;
  width: 70px;
  height: 70px;
  border-radius: 20px;
  background-color: #de0000;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 35px;
}
.dms__services-item {
  width: calc(49% - 20px);
  flex-grow: 1;
  display: flex;
  align-items: center;
  text-align: center;
  gap: 10px;
}
@media only screen and (max-width: 900px) {
  .dms__services-item {
    width: 100%;
    flex-direction: column;
  }
}
.dms__services-item h3 {
  font-style: italic;
}
.dms__partners {
  text-align: center;
  margin: 20px 0;
}
.dms__partners-list {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  margin: 40px 0;
}
@media only screen and (max-width: 900px) {
  .dms__partners-list {
    margin: 30px 0;
  }
}
.dms__partners-item {
  width: calc(24% - 10px);
  flex-grow: 1;
  height: 150px;
  position: relative;
}
@media only screen and (max-width: 900px) {
  .dms__partners-item {
    width: calc(49% - 10px);
  }
}
@media only screen and (max-width: 600px) {
  .dms__partners-item {
    width: 100%;
  }
}
.dms__partners-item img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.dms__biatlone {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 25px;
  font-weight: 600;
}
@media only screen and (max-width: 900px) {
  .dms__biatlone {
    flex-direction: column;
  }
}
.dms__about {
  margin: 30px 0;
}
.dms__about p {
  font-size: 17px;
}
.dms__advantages {
  margin: 30px 0;
}
.dms__advantages-wrapper {
  display: flex;
}
@media only screen and (max-width: 900px) {
  .dms__advantages-wrapper {
    flex-direction: column;
  }
}
.dms__advantages-wrapper img {
  width: 400px;
  height: 400px;
}
@media only screen and (max-width: 900px) {
  .dms__advantages-wrapper img {
    width: 100%;
    height: auto;
  }
}
.dms__advantages-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.dms__advantages-list li, .dms__advantages-list div {
  display: flex;
  gap: 10px;
  font-size: 20px;
}
.dms__advantages-list li i, .dms__advantages-list div i {
  font-size: 29px;
  display: flex;
  color: #de0000;
}
.dms__callback {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  background: linear-gradient(to bottom, rgb(255, 140, 0), rgb(222, 0, 0) 50%, rgb(255, 140, 0));
}
.dms__callback img {
  flex-shrink: 0;
  max-height: 500px;
}
@media only screen and (max-width: 1200px) {
  .dms__callback img {
    display: none;
  }
}
.dms__callback .callback {
  width: 50%;
}
@media only screen and (max-width: 1200px) {
  .dms__callback .callback {
    margin: 0 auto;
  }
}
@media only screen and (max-width: 900px) {
  .dms__callback .callback {
    width: 100%;
  }
}