.spec {
  padding: 40px 0;
  margin-top: 132px;
  margin-bottom: 32px;
}
@media only screen and (max-width: 1200px) {
  .spec {
    margin-top: 98px;
  }
}
@media only screen and (max-width: 900px) {
  .spec {
    margin-top: 70px;
  }
}
.spec__list {
  display: flex;
  flex-direction: column;
  gap: 40px;
  margin: 30px 0;
}
.spec__item {
  display: flex;
  gap: 40px;
  color: #000;
}
.spec__item p {
  color: rgb(36, 36, 36);
  opacity: 0.6;
}
.spec__item ul {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.spec__item ul li {
  font-weight: 400;
}
@media only screen and (max-width: 900px) {
  .spec__item {
    flex-direction: column;
  }
}
@media (hover: hover) {
  .spec__item:hover .spec__content span {
    color: #fff;
    background-color: #de0000;
  }
}
.spec__articles h3 {
  text-align: center;
  font-size: 34px;
  font-weight: 400;
}
.spec__articles ol {
  margin: 20px 0;
  box-shadow: 0 11.9104px 37.4328px rgba(148, 143, 143, 0.25);
  padding: 20px 40px;
  color: gray;
}
.spec__articles ol a {
  color: rgb(221, 153, 51);
  font-weight: 600;
}
@media (hover: hover) {
  .spec__articles ol a:hover {
    color: rgb(95, 61, 11);
  }
}
.spec__img {
  position: relative;
  flex-shrink: 0;
  width: 340px;
  height: 340px;
  border-radius: 30px;
  overflow: hidden;
}
@media only screen and (max-width: 900px) {
  .spec__img {
    width: 100%;
  }
}
.spec__img img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.spec__content {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.spec__content span {
  font-weight: 600;
  width: max-content;
  flex-shrink: 0;
  padding: 16px 32px;
  border: 2px solid #de0000;
  color: #de0000;
  background-color: #fff;
  border-radius: 10px;
  transition: background-color 0.3s, color 0.3s;
}
@media only screen and (max-width: 900px) {
  .spec__content span {
    width: 100%;
    text-align: center;
  }
}