.advantages__list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin: 20px 0;
}
.advantages__item {
  display: flex;
  align-items: center;
  gap: 20px;
  width: calc(33% - 10px);
  padding: 20px;
  box-shadow: 7px 7px 30px -5px rgba(0, 0, 0, 0.1);
}
@media only screen and (max-width: 1200px) {
  .advantages__item {
    width: calc(50% - 10px);
  }
}
@media only screen and (max-width: 900px) {
  .advantages__item {
    width: 100%;
  }
}
.advantages__item span {
  font-size: 50px;
}
.advantages__item div {
  display: flex;
  flex-direction: column;
}