.hero {
  position: relative;
  width: 100%;
  min-height: calc(100vh - 130px);
  background-image: url("../img/hero-bg.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  color: #fff;
  overflow: hidden;
  border-top: 5px solid #383838;
  padding: 40px 0;
  margin-top: 132px;
  margin-bottom: 32px;
}
@media only screen and (max-width: 1200px) {
  .hero {
    margin-top: 98px;
  }
}
@media only screen and (max-width: 900px) {
  .hero {
    margin-top: 70px;
  }
}
.hero__wrapper {
  display: flex;
  gap: 30px;
}
.hero__wrapper:not(:last-child) {
  margin-bottom: 60px;
}
@media only screen and (max-width: 600px) {
  .hero__wrapper {
    gap: 20px;
  }
  .hero__wrapper:not(:last-child) {
    margin-bottom: 30px;
  }
}
.hero__content {
  display: flex;
  flex-direction: column;
  gap: 32px;
  flex-grow: 1;
  color: #242424;
}
.hero__content p {
  width: 50%;
}
@media only screen and (max-width: 1200px) {
  .hero__content p {
    width: 100%;
  }
}
.hero__content h2 {
  width: 66%;
}
@media only screen and (max-width: 1200px) {
  .hero__content h2 {
    width: 100%;
  }
}
.hero__labels {
  display: flex;
  gap: 10px;
}
@media only screen and (max-width: 600px) {
  .hero__labels {
    flex-direction: column;
  }
}
.hero__labels span {
  font-size: 16px;
  font-weight: 700;
  text-align: center;
  flex-grow: 1;
  padding: 12px;
  border-radius: 8px;
  border: 4px solid #242424;
}
.hero__labels span:last-child {
  border-color: #de0000;
  color: #de0000;
}
.hero__link {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #de0000;
  font-size: 16px;
  font-weight: 600;
}
@media (hover: hover) {
  .hero__link:hover {
    color: #670101;
  }
}
.hero__features {
  display: flex;
  gap: 20px;
}
.hero__features h3 {
  position: relative;
  padding-bottom: 20px;
}
.hero__features h3::before {
  position: absolute;
  content: "";
  left: -10px;
  height: 50%;
  width: 2px;
  background-color: #242424;
}
@media only screen and (max-width: 600px) {
  .hero__features {
    flex-direction: column;
  }
}
.hero__features-item {
  padding-left: 10px;
}
.hero__img {
  flex-shrink: 0;
}
@media only screen and (max-width: 1000px) {
  .hero__img {
    display: none;
  }
}