.sitemap {
  padding: 240px 0;
}
.sitemap ul {
  list-style: square;
  display: flex;
  width: 50%;
  flex-direction: column;
  gap: 24px;
}
.sitemap ul li:not(:last-child) {
  border-bottom: 1px solid rgb(110, 110, 110);
}
.sitemap ul li a {
  text-transform: uppercase;
  font-size: 24px;
  line-height: 36px;
  font-weight: 600;
  color: #214250;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .sitemap ul li a:hover {
    opacity: 0.7;
  }
}