.description {
  display: flex;
  padding: 16px;
  background-color: rgba(24, 25, 31, 0.0784313725);
  margin: 32px 0;
  border-radius: 16px;
}
@media only screen and (max-width: 800px) {
  .description {
    flex-direction: column;
  }
}
.description__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px 20px;
  border-radius: 16px;
  transition: background-color 0.3s;
  text-align: center;
}
.description__item.is-current {
  background-color: rgba(255, 255, 255, 0.6392156863);
}
.description__item.is-current h2 {
  color: #de0000;
}
@media only screen and (max-width: 1000px) {
  .description__item {
    padding: 20px;
  }
}
.description__icon {
  display: flex;
  width: 110px;
  height: 110px;
  position: relative;
  margin-bottom: 42px;
}
.description__icon img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
@media only screen and (max-width: 1000px) {
  .description__icon {
    width: 80px;
    height: 80px;
    margin-bottom: 22px;
  }
}
.description__caption {
  font-size: 30px;
  font-weight: 700;
  line-height: 38.25px;
  text-align: center;
  transition: color 0.3s;
  text-transform: uppercase;
}
@media only screen and (max-width: 1000px) {
  .description__caption {
    font-size: 26px;
    font-weight: 600;
    line-height: 30px;
  }
}