.docs {
  padding: 40px 0;
  margin-top: 132px;
  margin-bottom: 32px;
}
@media only screen and (max-width: 1200px) {
  .docs {
    margin-top: 98px;
  }
}
@media only screen and (max-width: 900px) {
  .docs {
    margin-top: 70px;
  }
}
.docs__wrapper {
  display: flex;
  margin: 30px 0;
}
@media only screen and (max-width: 900px) {
  .docs__wrapper {
    flex-direction: column;
  }
}
.docs__link {
  display: inline-block;
  background-color: #32373c;
  padding: 14px 30px;
  color: #fff;
  border-radius: 30px;
  margin: 10px 0;
  text-align: center;
}
@media (hover: hover) {
  .docs__link:hover {
    color: #de0000;
  }
}
@media only screen and (max-width: 900px) {
  .docs__link {
    width: 100%;
  }
}