.contacts {
  margin: 60px 0;
  padding: 20px 0;
  background-image: url("../img/b3.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
}
@media only screen and (max-width: 1300px) {
  .contacts {
    margin: 30px 0;
  }
}
.contacts__wrapper {
  display: flex;
  gap: 20px;
  margin-top: 30px;
}
@media only screen and (max-width: 900px) {
  .contacts__wrapper {
    flex-direction: column;
  }
}
.contacts__item {
  position: relative;
  width: 50%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  border-radius: 30px;
  padding: 20px;
  border: 1px solid #f5f5f5;
  background-color: #fff;
}
@media only screen and (max-width: 900px) {
  .contacts__item {
    width: 100%;
  }
}
.contacts__item img {
  object-fit: cover;
}
.contacts__item a {
  color: #fff;
  font-size: 23px;
  line-height: 28px;
  margin-bottom: 32px;
}
.contacts__item span {
  font-weight: 600;
}
.contacts__item:first-child {
  background-color: #de0000;
  color: #fff;
}

.contacts-page {
  padding: 40px 0;
  margin-top: 132px;
  margin-bottom: 32px;
}
@media only screen and (max-width: 1200px) {
  .contacts-page {
    margin-top: 98px;
  }
}
@media only screen and (max-width: 900px) {
  .contacts-page {
    margin-top: 70px;
  }
}
.contacts-page__map-wrapper {
  display: flex;
  height: 500px;
  width: 100%;
  overflow: hidden;
  margin-top: 56px;
}
@media (max-width: 900px) {
  .contacts-page__map-wrapper {
    height: 300px;
    margin-top: 36px;
  }
}
.contacts-page__wrapper {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 900px) {
  .contacts-page__wrapper {
    flex-direction: column;
  }
}
.contacts-page__img {
  width: 50%;
}
@media (max-width: 900px) {
  .contacts-page__img {
    width: 100%;
  }
}
.contacts-page__list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 30px 0;
}
.contacts-page__item {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.contacts-page__icon {
  display: flex;
  width: 45px;
  height: 45px;
  flex-shrink: 0;
}
.contacts-page a, .contacts-page address {
  display: flex;
  gap: 20px;
  font-size: 17px;
  align-items: center;
  font-style: normal;
  color: #000;
}
@media (max-width: 900px) {
  .contacts-page a, .contacts-page address {
    font-size: 14px;
    gap: 10px;
  }
}
@media (hover: hover) {
  .contacts-page a:hover {
    color: #de0000;
  }
}
.contacts-page__data {
  margin-top: 32px;
  display: flex;
  gap: 20px;
  width: 100%;
  justify-content: space-between;
}
@media (max-width: 600px) {
  .contacts-page__data {
    flex-direction: column;
  }
}
.contacts-page__data-item {
  display: flex;
  flex-direction: column;
  padding-left: 10px;
  position: relative;
  font-size: 17px;
}
.contacts-page__data-item::before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  height: 100%;
  width: 2px;
  background-color: #000;
}