.about {
  margin-bottom: 32px;
}
.about--page {
  padding: 40px 0;
  margin-top: 132px;
  margin-bottom: 32px;
}
@media only screen and (max-width: 1200px) {
  .about--page {
    margin-top: 98px;
  }
}
@media only screen and (max-width: 900px) {
  .about--page {
    margin-top: 70px;
  }
}
.about__banner {
  position: relative;
  margin: 0 auto;
  width: max-content;
  flex-shrink: 0;
}
.about__banner div {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  inset: 0;
}
.about__banner div p {
  width: 60%;
  font-size: 22px;
}
@media only screen and (max-width: 1200px) {
  .about__banner {
    width: 100%;
  }
  .about__banner img {
    display: none;
  }
  .about__banner div {
    position: relative;
    left: auto;
    top: auto;
  }
  .about__banner div p {
    width: 100%;
    font-size: 28px;
    margin: 30px 0;
  }
}
.about h3 {
  font-size: 30px;
  font-weight: 700;
}
@media only screen and (max-width: 900px) {
  .about h3 {
    font-size: 24px;
  }
}
.about__wrapper {
  display: flex;
  gap: 20px;
}
@media only screen and (max-width: 900px) {
  .about__wrapper {
    flex-direction: column;
  }
}
.about__item {
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
  width: calc(50% - 10px);
  padding: 20px;
  border-radius: 30px;
  border: 2px solid #de0000;
  font-size: 17px;
}
@media only screen and (max-width: 900px) {
  .about__item {
    width: 100%;
    padding: 10px;
    border-radius: 16px;
    font-size: 14px;
  }
}

.about-list {
  padding: 20px 0;
  background-color: rgb(250, 250, 250);
}
.about-list__wrapper {
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
  margin: 20px 0;
}
.about-list__item {
  width: calc(32% - 15px);
}
@media only screen and (max-width: 900px) {
  .about-list__item {
    width: calc(50% - 15px);
  }
}
@media only screen and (max-width: 600px) {
  .about-list__item {
    width: 100%;
  }
}
.about-list__item h3 {
  position: relative;
  padding-bottom: 10px;
}
.about-list__item h3::before {
  position: absolute;
  content: "";
  bottom: 0;
  width: 50%;
  height: 2px;
  background-color: #de0000;
}

.about-info {
  padding: 20px 0;
}
.about-info__video {
  position: relative;
  height: 650px;
  width: 368px;
  flex-shrink: 0;
}
@media only screen and (max-width: 900px) {
  .about-info__video {
    margin: 0 auto;
  }
}
@media only screen and (max-width: 500px) {
  .about-info__video {
    width: 100%;
  }
}
.about-info__video video {
  position: absolute;
  inset: 0;
  height: 100%;
  width: 100%;
}
.about-info__wrapper {
  display: flex;
  gap: 30px;
  justify-content: space-between;
  margin: 20px 0;
}
@media only screen and (max-width: 900px) {
  .about-info__wrapper {
    flex-direction: column;
    gap: 20px;
  }
}
.about-info__item {
  padding: 20px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
  margin-bottom: 30px;
}
.about-info__content {
  flex-grow: 1;
}
.about-info__content a {
  color: #dd9933;
}
.about-info__banner {
  width: 100%;
  height: 500px;
  position: relative;
}
.about-info__banner img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}