.quality ul {
  list-style: none;
  padding: 64px;
  display: flex;
  gap: 64px;
}
.quality li {
  display: flex;
  flex-direction: column;
  width: calc(33.333% - 32px);
}
.quality h2 {
  font-size: 30px;
  font-weight: 700;
  line-height: 38.25px;
  text-align: left;
  text-transform: uppercase;
}
.quality p {
  font-size: 18px;
  font-weight: 300;
  line-height: 27px;
  text-align: left;
}
@media only screen and (max-width: 1300px) {
  .quality ul {
    list-style: none;
    padding: 64px 24px;
    display: flex;
    gap: 24px;
  }
  .quality li {
    width: calc(33.333% - 12px);
  }
  .quality h2 {
    font-size: 24px;
    line-height: 30px;
  }
  .quality p {
    font-size: 14px;
    line-height: 20px;
  }
}
@media only screen and (max-width: 700px) {
  .quality ul {
    padding: 64px 0;
    flex-direction: column;
  }
  .quality li {
    width: 100%;
  }
  .quality h2 {
    text-align: center;
  }
  .quality p {
    text-align: center;
  }
}