.works__list {
  display: flex;
  flex-direction: column;
}
.works__item {
  display: flex;
  gap: 64px;
  padding: 64px 0;
}
@media only screen and (max-width: 1500px) {
  .works__item {
    gap: 24px;
    padding: 24px 0;
  }
}
@media only screen and (max-width: 1200px) {
  .works__item {
    flex-direction: column-reverse;
  }
}
.works__previews {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  width: calc(40% - 32px);
  height: 500px;
}
@media only screen and (max-width: 1500px) {
  .works__previews {
    width: calc(40% - 12px);
    height: 400px;
  }
}
@media only screen and (max-width: 1200px) {
  .works__previews {
    width: 100%;
    height: 180px;
    flex-wrap: nowrap;
  }
}
@media only screen and (max-width: 900px) {
  .works__previews {
    overflow-x: scroll;
  }
}
.works__previews a {
  width: calc(50% - 6px);
  height: calc(50% - 6px);
  position: relative;
  overflow: hidden;
  border-radius: 16px;
}
@media only screen and (max-width: 1200px) {
  .works__previews a {
    width: calc(25% - 6px);
    height: 100%;
  }
}
@media only screen and (max-width: 900px) {
  .works__previews a {
    min-width: 220px;
    flex-shrink: 0;
  }
}
.works__previews a img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s;
  object-position: center;
}
@media (hover: hover) {
  .works__previews a:hover img {
    transform: scale(1.1);
  }
}
.works__content {
  width: calc(60% - 32px);
}
@media only screen and (max-width: 1500px) {
  .works__content {
    width: calc(60% - 12px);
  }
}
@media only screen and (max-width: 1200px) {
  .works__content {
    width: 100%;
  }
}
.works__content h2 {
  font-size: 42px;
  font-weight: 600;
  line-height: 53.55px;
  text-align: left;
  text-transform: uppercase;
}
@media only screen and (max-width: 1500px) {
  .works__content h2 {
    font-size: 32px;
    font-weight: 600;
    line-height: 43.55px;
  }
}
@media only screen and (max-width: 900px) {
  .works__content h2 {
    font-size: 24px;
    font-weight: 600;
    line-height: 33.55px;
  }
}