.footer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(36, 36, 36);
  color: #fff;
  width: 100%;
}
.footer .header__social-icon {
  display: flex;
  flex-shrink: 0;
  width: 30px;
  height: 30px;
  align-items: center;
  justify-content: center;
}
.footer .header__social-links {
  display: flex;
  justify-content: space-between;
}
.footer .header__callback {
  text-align: center;
  font-size: 15px;
  border-bottom: 2px dashed red;
  cursor: pointer;
}
.footer .header__contacts {
  width: max-content;
}
.footer .header__contacts:last-child {
  margin-left: 0;
  flex-direction: column-reverse;
}
@media only screen and (max-width: 1350px) {
  .footer .header__contacts:last-child {
    display: flex;
  }
}
@media only screen and (max-width: 1000px) {
  .footer .header__contacts {
    display: flex;
    margin: 0;
    width: max-content;
  }
  .footer .header__contacts:last-child {
    display: flex;
    margin: 0;
  }
}
.footer .header__contacts a {
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s;
  text-align: right;
  color: #fff;
}
@media only screen and (max-width: 700px) {
  .footer .header__contacts a {
    font-size: 12px;
  }
}
.footer .header__contacts span {
  display: flex;
  align-items: center;
  gap: 10px;
  text-transform: uppercase;
}
@media only screen and (max-width: 700px) {
  .footer .header__contacts span {
    font-size: 12px;
  }
}
@media (hover: hover) {
  .footer .header__contacts a:hover {
    color: #de0000;
  }
}
@media only screen and (max-width: 700px) {
  .footer .header__icon {
    width: 30px;
    height: 30px;
  }
}
.footer__up {
  position: absolute;
  bottom: 20px;
  right: 10px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #de0000;
  transition: background-color 0.3s;
  padding-top: 4px;
}
@media (hover: hover) {
  .footer__up:hover {
    background-color: #c72a28;
  }
}
.footer__caption {
  font-size: 24px;
  font-weight: 600;
  line-height: 30.6px;
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 10px;
}
@media only screen and (max-width: 600px) {
  .footer__caption {
    font-size: 14px;
    line-height: 18px;
  }
}
.footer__bottom {
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  text-align: center;
}
.footer__bottom a {
  font-size: 18px;
  color: #fff;
}
@media (hover: hover) {
  .footer__bottom a:hover {
    color: #de0000;
  }
}
.footer__wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.footer__main {
  padding: 30px 0;
}
.footer__nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.footer__nav a {
  font-size: 20px;
  line-height: 25px;
  color: #fff;
  transition: color 0.3s;
  opacity: 0.6;
}
@media (hover: hover) {
  .footer__nav a:hover {
    color: #de0000;
  }
}
@media only screen and (max-width: 600px) {
  .footer__nav a {
    font-size: 14px;
    line-height: 18px;
  }
}
@media only screen and (max-width: 900px) {
  .footer__nav {
    gap: 16px;
  }
}
.footer__address {
  font-size: 14px;
  line-height: 22.4px;
  font-style: normal;
}
.footer__worktime {
  font-size: 20px;
  font-weight: 400;
  line-height: 25.5px;
  color: #fff;
}
@media only screen and (max-width: 769px) {
  .footer__worktime {
    font-size: 12px;
    line-height: 14px;
  }
}
.footer__tel {
  font-size: 32px;
  font-weight: 700;
  line-height: 40.8px;
  color: #fff;
  transition: color 0.3s;
}
@media (hover: hover) {
  .footer__tel:hover {
    color: #de0000;
  }
}
@media only screen and (max-width: 900px) {
  .footer__tel {
    font-size: 24px;
    font-weight: 600;
    line-height: 28px;
  }
}
@media only screen and (max-width: 600px) {
  .footer__tel {
    font-size: 14px;
    font-weight: 600;
    line-height: 18px;
  }
}
.footer__mail {
  font-size: 20px;
  font-weight: 500;
  line-height: 25.5px;
  color: #fff;
  text-decoration: underline;
  transition: color 0.3s;
}
@media (hover: hover) {
  .footer__mail:hover {
    color: #842726;
  }
}
@media only screen and (max-width: 600px) {
  .footer__mail {
    font-size: 14px;
    font-weight: 500;
    line-height: 18px;
  }
}
.footer__pages {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.footer__pages a {
  font-size: 20px;
  font-weight: 500;
  line-height: 25.5px;
  color: #fff;
  transition: color 0.3s;
}
@media (hover: hover) {
  .footer__pages a:hover {
    color: #de0000;
  }
}
@media only screen and (max-width: 600px) {
  .footer__pages a {
    font-size: 14px;
    line-height: 18px;
  }
}
@media only screen and (max-width: 769px) {
  .footer__pages {
    gap: 16px;
    font-size: 16px;
    font-weight: 400;
    line-height: 18px;
  }
}
.footer__columns {
  display: flex;
  justify-content: center;
  gap: 40px;
}
@media only screen and (max-width: 1600px) {
  .footer__columns {
    padding: 0;
  }
}
@media only screen and (max-width: 1300px) {
  .footer__columns {
    padding: 0 60px;
  }
}
@media only screen and (max-width: 900px) {
  .footer__columns {
    gap: 24px;
    padding: 0;
  }
}
.footer__column {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.footer__column--info {
  align-items: center;
  gap: 24px;
}
@media only screen and (max-width: 1300px) {
  .footer__column--info {
    align-items: flex-start;
  }
}
@media only screen and (max-width: 700px) {
  .footer__column--info {
    gap: 16px;
  }
}
.footer__column--rec span {
  margin-bottom: 16px;
}
.footer__group {
  display: contents;
}
@media only screen and (max-width: 1300px) {
  .footer__group {
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
}