.articles--page {
  padding: 40px 0;
  margin-top: 132px;
  margin-bottom: 32px;
}
@media only screen and (max-width: 1200px) {
  .articles--page {
    margin-top: 98px;
  }
}
@media only screen and (max-width: 900px) {
  .articles--page {
    margin-top: 70px;
  }
}
.articles__wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin: 30px 0;
}
@media only screen and (max-width: 900px) {
  .articles__wrapper {
    flex-direction: column;
  }
}
.articles__item {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: calc(33% - 10px);
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
  padding-bottom: 20px;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  color: rgb(163, 87, 37);
}
@media only screen and (max-width: 900px) {
  .articles__item {
    width: 100%;
  }
}
@media (hover: hover) {
  .articles__item:hover {
    color: #de0000;
  }
}
.articles h3 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  padding: 0 20px;
}
.articles span {
  padding: 0 20px;
}
.articles span:last-child {
  margin-top: auto;
}
.articles__preview {
  height: 222px;
  width: 100%;
  position: relative;
}
@media only screen and (max-width: 1200px) {
  .articles__preview {
    height: 200px;
  }
}
.articles__preview img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}