@charset "UTF-8";
.faq {
  margin: 30px 0;
}
.faq__subcaption {
  font-size: 20px;
  font-weight: 300;
  line-height: 30px;
  text-align: left;
  color: rgba(24, 25, 31, 0.6392156863);
}
.faq__wrapper {
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: 100%;
}
@media only screen and (max-width: 900px) {
  .faq__wrapper {
    flex-direction: column;
  }
}
.faq__content {
  display: flex;
  width: 100%;
  flex-direction: column;
  gap: 12px;
}
@media only screen and (max-width: 900px) {
  .faq__content {
    width: 100%;
  }
}
.faq__content details {
  box-shadow: 0 11.9104px 37.4328px rgba(148, 143, 143, 0.25);
  overflow: hidden; /* Скрываем избыточное содержимое */
  padding: 16px;
  background-color: rgba(255, 255, 255, 0.6392156863);
}
@media only screen and (max-width: 1200px) {
  .faq__content details {
    padding: 16px;
  }
}
.faq__content details > div {
  padding: 24px 40px;
  font-size: 18px;
  font-weight: 300;
  line-height: 27px;
  text-align: left;
}
@media only screen and (max-width: 1200px) {
  .faq__content details > div {
    padding: 16px 0;
  }
}
.faq__content summary {
  display: flex;
  justify-content: flex-start;
  cursor: pointer;
  font-size: 24px;
  font-weight: 400;
  line-height: 36px;
  text-align: left;
  list-style: none; /* Убрать маркеры */
}
.faq__content summary::-webkit-details-marker {
  display: none; /* Скрыть стандартный маркер */
}
.faq__content summary::before {
  content: "+"; /* Добавить знак + */
  color: #de0000;
  font-size: 30px;
  margin-right: 24px;
}
@media only screen and (max-width: 1200px) {
  .faq__content summary::before {
    font-size: 24px;
    margin-right: 14px;
  }
}
.faq__content details[open] summary::before {
  content: "-"; /* Изменить на знак - при открытии */
}