.review-form {
  margin: 60px 0;
}
@media only screen and (max-width: 1300px) {
  .review-form {
    margin: 30px 0;
  }
}
.review-form__notice {
  display: inline-block;
  color: rgb(221, 221, 221);
  margin: 20px 0;
}
.review-form__wrapper {
  display: flex;
  gap: 60px;
}
@media only screen and (max-width: 1300px) {
  .review-form__wrapper {
    gap: 20px;
  }
}
@media only screen and (max-width: 900px) {
  .review-form__wrapper {
    flex-direction: column;
  }
}
.review-form__content {
  display: flex;
  flex-direction: column;
}
@media only screen and (max-width: 900px) {
  .review-form__content {
    flex-direction: column-reverse;
  }
}
.review-form__img {
  width: 500px;
}
@media only screen and (max-width: 1300px) {
  .review-form__img {
    width: 300px;
  }
}
@media only screen and (max-width: 900px) {
  .review-form__img {
    width: 100%;
  }
}
.review-form__links {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: max-content;
}
.review-form__links a {
  color: #de0000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  font-size: 16px;
  transition: opacity 0.3s;
}
.review-form__links a i {
  font-size: 40px;
}
.review-form__links a .fa-yandex {
  margin-left: 8px;
}
@media (hover: hover) {
  .review-form__links a:hover {
    opacity: 0.6;
  }
}
@media only screen and (max-width: 900px) {
  .review-form__links {
    margin: 20px auto;
  }
}